// General About dialog styling
// Depends on styles loaded via /platform/commonUI/general/res/sass/startup-base.scss
.l-about {
	&.abs {
		overflow: auto;
	}
	$contentH: 200px;
	.l-splash {
		position: relative;
		height: 45%;
	}
	.l-content {
		position: relative;
		margin-top: $interiorMarginLg;
	}
}

.s-about {
	line-height: 120%;

	a {
		color: $colorAboutLink;
	}
	.s-description,
	.s-button {
		line-height: 2em;
	}
	.l-licenses-software {
		.l-license-software {
			border-top: 1px solid $colorInteriorBorder;
			padding: 0.5em 0;
			&:first-child {
				border-top: none;
			}
			em {
				color: pushBack($colorBodyFg, 20%);
			}
			h3 {
				font-size: 1.25em;
			}
			.s-license-text {
				font-size: 0.9em;
			}
		}
	}
}
